<template>
  <div id="ServiceDetail">
    <div class="banner container-fuild text-center">相关服务</div>
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-3" id="myScrollspy">
          <ul class="nav nav-tabs nav-stacked center-block" id="myNav">
            <p>特色服务</p>
            <li
              :class="item.id==id?'active':''"
              v-for="(item,index) in serviceNavList"
              :key="index"
            >
              <a :href="'#'+item.id">{{item.title}}</a>
            </li>
          </ul>
        </div>
        <div class="col-xs-12 col-sm-12 col-md-9 content  wow zoomIn">
          <div class="content-block" v-for="(item,index) in serviceContentList" :key="index">
            <h2 :id="item.id">
              {{item.title}}
              <small>/ {{item.eng_title}}</small>
            </h2>
            <div v-html="item.content"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from "wowjs";
export default {
  name: "ServiceDetail",
  data() {
    return {
      id: "section-1",
      serviceNavList: [
        {
          id: "section-1",
          title: "芯片定制开发"
        },
        {
          id: "section-2",
          title: "芯片销售服务"
        },
        {
          id: "section-3",
          title: "芯片生态圈"
        },
        {
          id: "section-4",
          title: "芯片一体化"
        }
      ],
      serviceContentList: [
        {
          id: "section-1",
          title: "芯片定制开发",
          eng_title: "Chip development",
          content:
            "<h3>专业芯片方案</h3><p>抚州宇智通电子有限公司为帮助客户加快产品上市时间，推出芯片定制业务。该业务模式将向消费类电子和普及计算市场的客户提供专用芯片。将通过灵活的客户服务模式(CEM)为客户提供可升级的高性能、低成本、快速有效的芯片方案。</p>"
        },
        {
          id: "section-2",
          title: "芯片销售服务",
          eng_title: "Chip sales",
          content:
            "<h3>渠道</h3><p>第一，连接制造商和终端用户，渠道成为产品或服务传递的枢纽。第二，提高交易效率，降低交易成本。对制造商而言，单点市场由一对多变成一对一，大大提高经营效率，原本只能做到以一（销售员）到十（消费者），通过渠道可以实现从一到百甚至千。</p><h3>直通厂家</h3><p>第三，充分发挥渠道成员间的协同作用，共享渠道资源。在物流市场活动、售后服务等方面，整合渠道资源，共同发展。第四，分担厂家的压力，规避市场风险。渠道分担了厂家的库存、账期、售前支持和售后服务的压力，在资金和人力资源上给予厂家有力的支持，有效地规避了还款风险和存货风险。</p>"
        },
        {
          id: "section-3",
          title: "芯片生态圈",
          eng_title: "eCommerce Site",
          content:
            "<h3>不同品类芯片</h3><p>公司生产销售各类用品的芯片，包括手机、电脑、其他类型机器。</p>"
        },
        {
          id: "section-4",
          title: "芯片一条龙",
          eng_title: "iOS App Dev",
          content:
            "<h3>生产-营销一条龙服务</h3><p>庞大的工厂支撑，各类渠道保障，丰富的用户积累。</p>"
        }
      ]
    };
  },
  mounted() {
    this.id = this.$route.params.id;
    var top = document.getElementById(this.id).offsetTop;
    $(window).scrollTop(top + 300);
    $("#myNav").affix({
      offset: {
        top: 300
      }
    });
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
ul.nav-tabs {
  width: 200px;
  margin-top: 40px;
  border-radius: 4px;
  background: #fff;
  z-index: 99999;
  border: 1px solid #474747;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.067);
}
ul.nav-tabs li {
  text-align: center;
  margin: 0;
  border-top: 1px solid #474747;
}
ul.nav-tabs p {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  background: #474747;
  margin: 0;
  padding: 10px 0;
}
ul.nav-tabs li:first-child {
  border-top: none;
}
ul.nav-tabs li a {
  margin: 0;
  padding: 8px 16px;
  border-radius: 0;
}
ul.nav-tabs li.active a,
ul.nav-tabs li.active a:hover {
  color: #fff;
  background: #474747;
  border: 1px solid #474747;
}
ul.nav-tabs li:first-child a {
  border-radius: 4px 4px 0 0;
}
ul.nav-tabs li:last-child a {
  border-radius: 0 0 4px 4px;
}
ul.nav-tabs.affix {
  top: 30px;
}
.content-block {
  margin: 50px 0;
}
.content-block > h2 {
  padding: 20px 0;
  border-bottom: 1px solid #ccc;
}
</style>



